@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("Fira Sans");
  src: url("../fonts/FiraSans-Regular.ttf") format("ttf"); }

@font-face {
  font-family: "Fira Sans";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: local("Fira Sans");
  src: url("../fonts/FiraSans-Medium.ttf") format("ttf"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: local("Montserrat");
  src: url("../fonts/Montserrat-Bold.ttf") format("ttf"); }

@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: local("Raleway");
  src: url("../fonts/Raleway-ExtraBold.ttf") format("ttf"); }

